const blog_slide = document.getElementById("front-page-blog-slides");
const blog_btn_left = document.getElementById("blog-nav-btn-left");
const blog_btn_right = document.getElementById("blog-nav-btn-right");

var blog_scrlX = blog_slide.scrollWidth / 6;
blog_btn_left.onclick = () => {blog_slide.scrollBy({left:-scrlX, behavior:"smooth" });}
blog_btn_right.onclick = () => {blog_slide.scrollBy({left:scrlX, behavior:"smooth" });}

var blog_tmpVar = blog_scrlX;
var y =0;
setInterval(() => {
    if(blog_slide.scrollLeft == 0){
        blog_tmpVar = blog_scrlX;
        y=0;
    }
    else if(y >= blog_slide.scrollWidth-blog_scrlX-2){
        blog_tmpVar = -blog_scrlX;
    }
    blog_slide.scrollBy({left:blog_tmpVar, behavior:"smooth" });
    y = y + blog_scrlX;
}, 3511); 

let show = (slide_id) =>
{
    const slide_btn = document.getElementById(slide_id+"-btn");
    const slide = document.getElementById(slide_id);

    const slide_length = document.getElementById("carousel-slides").childElementCount;

    for(i=1; i<=slide_length; i++){

        if(slide_id !== "slide"+i){
            document.getElementById("slide"+i).classList.remove("slide-show");
            document.getElementById("slide"+i).classList.add("slide-hide");
            document.getElementById("slide"+i+"-btn").classList.remove("slide-btn-active");

        }
        else{
            slide.classList.add("slide-show");
            slide.classList.remove("slide-hide");
            slide_btn.classList.add("slide-btn-active");
        }
    }
}

const slide_length = document.getElementById("carousel-slides").childElementCount;
let slideshow = (i) =>
{
    show("slide"+i)
    if(i==slide_length){
        n=1;
    }
    else{
        n = i+1;
    }
    // add logical exp: outof view no exec
    setTimeout(function(){slideshow(n)}, 5000);
}
slideshow(1);
